// Fichero src/components/ImageReader.js
import { useRef } from 'react';

/*
El orden cronolgico en el que se ejecuta este componente es:
- Se renderiza la primera vez
  - Se crea la referencia fileElement
  - Se crea el lector de ficheros fileReader
  - Se pinta el input file
- Cuando la usuaria selecciona una imagen:
  - Se ejecuta la funcin handleFile
    - Esta funcin ejecuta fileReader.readAsDataURL(selectedFile)
    - La funcin readAsDataURL lee el contenido de la imagen
    - La funcin readAsDataURL es asncrona. Cuando termine lanzar un evento 'load'.
- Pasados unos milisegundos, cuando se lance el evento 'load':
  - Se ejecutar la funcin getImage
    - Que subir los datos de la imagen por lifting
*/

const ImageReader = (props) => {
  // Creamos una referencia al input file para poder leer su contenido ms tarde
  const fileElement = useRef();

  // Creamos un lector de ficheros
  // FileReader es una funcionalidad nativa de JavaScript, bscala en Internet si te atreves
  const fileReader = new FileReader();

  // Cuando la usuaria selecciona una imagen se ejecuta esta funcin
  const handleFile = () => {
    // fileElement.current es literalmente el input file
    // Sacamos su propiedad files que es un array con todas las imgenes seleccionadas por la usuaria
    // Como solo queremos la primera imagen seleccionada guardamos el primer elemento de files
    const selectedFile = fileElement.current.files[0];

    // Si la usuaria ha seleccionado al menos una imagen, selectedFile ser diferente de undefined
    if (selectedFile) {
      // Le decimos al lector de ficheros que lea el fichero seleccionado por la usuaria
      fileReader.readAsDataURL(selectedFile);
      // Cuando esta accin termine, fileReader lanzar el evento 'load'
    }
  };

  // Esta funcin se ejecuta cuando fileReader lanza el evento 'load'
  const getImage = () => {
    // Cuando la imagen ya est lista en fileReader.result tendremos su contenido
    // Hacemos lifting de este contenido hacia arriba
    props.handleImage(fileReader.result);
  };

  // Escuchamos el evento load de fileReader y cuando se lance lo manejamos con la funcin getImage
  fileReader.addEventListener('load', getImage);

  return (
    <label
      htmlFor="profile-image"
      className="fill__label fill__label--profileimage">
      <div className="fill__label__container ">
        <label
          className="fill__label__container--button"
          htmlFor="img-selector">
          Aadir imagen
        </label>
        <input
          ref={fileElement}
          type="file"
          name="photo"
          id="img-selector"
          className="action__hiddenField js__profile-upload-btn js_input_photo fill__label__text--profileimage collapse"
          required
          onChange={handleFile}
        />

        <div
          className="fill__label__container--box js__profile-preview"
          style={{ backgroundImage: `url(${props.photo})` }}></div>
      </div>
    </label>
  );
};

export default ImageReader;
